.pi-holder {
  width: 400px;
  box-sizing: border-box;
  margin: 4px;
  font-family: Arial, Helvetica, sans-serif;
}
.pi-holder .header {
  background-color: #008390;
  padding: 6px;
  font-size: 22px;
  color: white;
}
.pi-holder .select {
  width: 100%;
  height: 28px;
  display: block;
  padding: 4px;
  margin: 4px 0px;
}
.pi-holder .total-cost {
  margin: 4px 0;
  margin-right: 0;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.pi-holder .total-cost .price {
  font-size: 20px;
  font-weight: bold;
}
